import { createComponentFromModel } from 'core/loadEntities';

export default function (entity) {

      const spriteSheets = entity.get('sprites');
      if(spriteSheets && spriteSheets.parts && !spriteSheets.parts.discover) {

        let sprite = {
            "name": "sprites",
            "scope": "common",
            "params": {
                "parts": {
                    "discover": {
                        "source": "discover-idle",
                        "size": {
                            "width": 32,
                            "height": 32
                        },
                        "destination": {
                            "x": 0,
                            "y": -64
                        },
                        "opacity": 1
                    }
                }
            }
        }


        const newSprite = createComponentFromModel.call(this, sprite);

        spriteSheets.parts.discover = newSprite.parts.discover

          const entityForces = entity.get('forces');
        if(entityForces.parts.removeDiscover) delete entityForces.parts.removeDiscover
        this.scripts.snippets['aJam']['timeoutRemoveDiscover'].call(this, entity);
      }
}
